.statusContainer {
    margin-bottom: 24px;
  display: flex;
  align-self: flex-start;
  flex-grow: 0;
 }
 /* Default */
.textStatus {
  position: relative;
  color: #888888;
  font-weight: 500;
  font-size: 10px;
  line-height: 1.36;
  text-align: left;
  letter-spacing: 0.04em;
  margin-bottom: 12px;
  width: 80px;
}
.textStatus:not(:last-child) {
  margin-right: 12px;
}
.textStatus::after {
    position: absolute;
    content: "";
    background-color: #cce4fb;
  width: 80px;
  height: 8px;
  border-radius: 8px;
  left: 0;
  bottom: -16px;
  text-align: center;
}


@media screen and (min-width: 768px) {
  .statusContainer {
    margin-bottom: 40px;
  }
  .textStatus {
    font-size: 15px;
    line-height: 1.21;
    margin-bottom: 16px;
    min-width: 120px;
  }
  .textStatus::after {
    width: 120px;
  }

  .textStatus:not(:last-child) {
  margin-right: 16px;
}
}

