.formContainer {
    width: 280px;
    background-color: #fff;
    border-radius: 40px;
    box-shadow: 3px 8px 14px 0px rgba(136, 198, 253, 0.19);
padding: 20px 8px 16px 8px;
margin-left: auto;
margin-right: auto;
display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start;
}

@media screen and (min-width: 768px) {
    .formContainer {
width: 458px;
padding: 20px 32px;

    }
}